*{
    box-sizing: border-box;
}
.quoteApp{
    height: 350px;
    padding: 20px;
}
.main{
background-color: white;
width: 80%;
border-radius: 10px;
margin: 10px auto;
padding: 20px;
/* height: 0px; */
text-align: justify;
/* background-color: rgb(69, 96, 219); */
}
strong{
    font-size: 30px;
}
.quote{
    height: 160px;
    /* background-color: blue; */
    /* margin-bottom:10px ; */
}

h4{
    width: fit-content;
    /* background-color: blue; */
    margin-left: auto;
}
.btns{
     /* background-color: red; */
     height: 50px;
     padding: 10px;
     position: relative;
}
a{
text-decoration: none;
padding: 3% 6%;
margin: 3%;
border-radius: 5px;
}
a:hover{
    box-shadow: 0 2px 3px  black;
    
}
.btns button{
    padding: 3%;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
    border-radius: 5px;
    /* margin: 3% 0 3% 30%; */
}
.tooltipTwitter, .tooltipSpanTumblr{
    background-color: #fff;
    /* position: relative; */
    position: fixed;
    border: 1px solid black;
    font-size: smaller;
    padding:0 3px;
    display: none;
}

a:hover+ .tooltipTwitter , a:hover+ .tooltipSpanTumblr {
    display: block;
}
button:hover{
    box-shadow: 0 2px 3px  black;
}
.nameRumiani{
    width: fit-content;
    margin: auto;
}
@media(max-width:499px){
    .main{
        width: 80%;
        }
}