.container{
    top: 40%;
    left: 100px;
    position: relative;
    transform: translate(-50%,-50%);
    margin:   20px auto;
    width: 256px;
    text-align: center;
    border: 3px solid black;
    background: gray;
    border-radius: 5px;
}

.keypad{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(60px,auto);
    /* background-color: red; */
}

.output{
    height: 25px;
    width: 249px;
    background-color: white;
    border: 0px;
    color: black;
    text-align: right;
    letter-spacing: 1px;
}
#clear{
    grid-column: 1/3 ;
    grid-row: 1;
}
#result{
    grid-column: 3/5 ;
    grid-row: 5 ;
}

#backspace{

}

.calbtn{
    margin: 5px ;
    border: none;
    cursor: pointer;
}
input:focus,
input:active,
.calbtn:focus,
.calbtn:active{
    outline: none;
}