.alarm{
    text-align: center;
}

.timerIcon{
    cursor: pointer;
    background-color: gray;
    padding: 2px 10px;
    margin: 10px;
    border-radius: 5px;
    font-size: x-large;
}
.timerIcon:hover{
    background-color: indianred;
    border: 1px solid black;
    box-sizing: border-box;
    color: white;
}