.length{
    display: flex;
   flex-wrap: wrap;
   margin: 0 auto;
   width: 22%;
   /* background-color: red; */
   text-align: center;
   
}

.timerIcon{
    cursor: pointer;
    background-color: gray;
    padding: 2px 10px;
    margin: 10px;
    border-radius: 5px;
    font-size: x-large;
}
.timerIcon:hover{
    background-color: indianred;
    border: 1px solid black;
    box-sizing: border-box;
    color: white;
}