.keyboard{
    /* border: 1px solid black; */
    display: flex;
    flex-wrap: wrap;
    width: 50%;
    height: 400px;    
    padding: 3%;
    align-items: center;
    }

    .eachBtn{
        
        background-color: red;
        margin: 2%;
        padding: 10%;
        width: 28%;
        height: 28%;
        cursor: pointer;
        text-align: center;
        border-radius: 5px;
    }
.eachBtn:active{
    background-color: rgb(106, 32, 243);
    box-shadow: 2px 2px 3px black;
}

@media (max-width:674px){
    .keyboard{
        width: 80%;

        margin: auto;
        /* padding: 30px; */
    }
    .eachBtn{
        width: 28%;
        height: 28%;
    }
}

.playing{
    background-color: rgb(106, 32, 243);
    box-shadow: 2px 2px 3px black;
    color: white;
}